.products-solution {
  padding-top: 72px;
  padding-bottom: 72px;

  @include tablet {
    padding-top: 56px;
    padding-bottom: 56px;
  }

  @include medium-tablet {
    padding-top: 32px;
  }

  @include mobile {
    padding-bottom: 48px;
  }

  &__title {
    margin-bottom: 12px;

    @media (max-width: 360px) {
      font-size: 26px;
    }
  }

  &__descr {
    margin-bottom: 48px;
    max-width: 840px;

    @include small-tablet {
      margin-bottom: 32px;
    }
  }

  &-tabs {
    display: flex;
    @include tablet {
      flex-direction: column;
    }
  }

  .tabs__panel {
    display: none;
  }

  .tabs__nav {
    width: 196px;
    flex-shrink: 0;
    list-style: none;
    margin: 0;
    padding: 0;
    margin-right: 24px;
    @include tablet {
      width: 100%;
      margin-right: 0;
      margin-bottom: 24px;
      display: flex;
      align-items: center;
      border-bottom: 1px solid var(--grey-200);
      overflow-x: auto;
      white-space: nowrap;
      scrollbar-width: none;
        /*mozilla*/
      &::-webkit-scrollbar {
        display: none;
        /*chrome*/
      }
    }
  }

  .tabs__nav-title {
    margin-bottom: 24px;
    font-weight: 600;
    font-size: 24px;
    line-height: 121.9%;
    color: var(--base-grey);

    @include tablet {
      margin-bottom: 0;
      margin-right: 32px;
      font-size: 20px;
    }

    @include mobile {
      font-weight: 700;
      font-size: 16px;
      margin-right: 24px;
    }
  }

  .tabs__nav-item:not(:last-child) {
    margin-bottom: 16px;

    @include tablet {
      margin-bottom: 0;
      margin-right: 24px;
    }

    @include mobile {
      margin-right: 16px;
    }
  }

  .tabs__nav-btn {
    font-weight: 500;
    font-size: 20px;
    line-height: 121.9%;
    color: var(--grey-400);
    background-color: transparent;

    @include tablet {
      padding: 16px 0;
      font-weight: 600;
      font-size: 16px;
    }
  }

  .tabs__panel--active {
    display: block;
  }

  .tabs__nav-btn--active {
    color: var(--base-grey);
    border-bottom: 2px solid var(--cyan-400);
  }

  .enterprise__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: auto;
    gap: 24px;
    margin-bottom: 0;

    @media (max-width: 1200px) {
      grid-template-columns: repeat(2, 1fr);
    }

    @include small-tablet {
      grid-template-columns: repeat(1, 1fr);
      gap: 24px;
    }
  }

  .enterprise__card {
    display: flex;
    flex-direction: column;
    border-radius: 12px;
    box-shadow: var(--shadow-4);
    background-color: var(--base-white);
    height: 100%;
    transition: box-shadow 0.3s ease-in;

    &:hover {
      box-shadow: var(--shadow-1);
    }

    &-img {
      display: none;
    }

    &-icon {
      display: block;
      width: 48px;
      height: 48px;
      margin-top: 24px;
      margin-left: 24px;

      img {
        width: 100%;
        height: 100%;
      }
    }

    &-box {
      padding: 24px;
      display: flex;
      flex-direction: column;
      height: 100%;
    }

    &-content {
      margin-bottom: 0;
    }

    &-title {
      font-weight: 600;
      font-size: 24px;
      line-height: 121.9%;
      margin-bottom: 12px;
    }

    &-descr {
      font-size: 18px;
      line-height: 156%;
      color: var(--grey-500);

      @media (max-width: 420px) {
        font-size: 16px;
      }
    }

    &-btn {
      display: none;
    }
  }

  .enterprise__btn1 {
    @include small-tablet {
      display: none;
    }
  }

  .enterprise__btn2 {
    display: none;

    @include small-tablet {
      display: none;
    }
  }
}
