.heading {
  margin-top: 48px;
  margin-bottom: 72px;

  @include tablet {
    margin-bottom: 42px;
  }

  @include medium-tablet {
    margin-bottom: 32px;
  }

  @include small-tablet {
    margin-top: 32px;
    margin-bottom: 24px;
  }

  @include mobile {
    margin-top: 24px;
    margin-bottom: 12px;
  }

  &__container {
    width: 100%;
    min-height: 240px;
    padding: 50px 72px;
    border-radius: 24px;
    background-color: var(--grey-100);
    background-image: url('../img/page-products/heading-img.png');
    background-size: 40% auto;
    background-repeat: no-repeat;
    background-position: right;

    @include tablet {
      padding: 40px 45px;
      min-height: 216px;
    }

    @media (max-width: 950px) {
      min-height: auto;
    }

    @include small-tablet {
      padding: 35px;
      border-radius: 12px;
    }
    @include mobile {
      padding: 30px 24px;
      background-image: none;
    }
  }

  &__content {
    max-width: 490px;

    @media (max-width: 950px) {
      max-width: 370px;
    }
    @include small-tablet {
      max-width: 330px;
    }
    @media (max-width: 650px) {
      max-width: 300px;
    }
    @include mobile {
      text-align: center;
      max-width: 100%;
    }
  }

  &__title {
    margin-bottom: 12px;
  }

  &__descr {
    @include tablet {
      font-size: 22px;
    }
    @media (max-width: 950px) {
      font-size: 18px;
    }
    @include small-tablet {
      font-size: 16px;
    }
    @include mobile {
      text-align: center;
    }
  }
}
